.link-input {
    display: flex;
    flex-direction: row;
    margin: 0;
    gap: 20px;
    width: 100%;
    align-items: flex-end;
  }
  .link-input__left-box {
    max-width: 316px;
    width: 100%;
  }
  .link-input__right-box {
    max-width: 265px;
    width: 100%;
  }
  .link-input__label-container {
    display: flex;
  }
  
  .link-input__label {
    margin-bottom: 12px;
    text-align: left;
    color: #000;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.21;
  }
  // .link-input__select-wrapper {
  //   position: relative;
  // }
  // .link-input__select-wrapper::after {
  //   content: url(../../../../img/select-arrow.svg);
  //   position: absolute;
  //   bottom: 12px;
  //   right: 16px;
  //   pointer-events: none;
  // }
  .link-input__field{
    appearance: none;
    border-radius: 16px;
    border: 1px solid #9f9f9f;
    outline: #000;
    width: 95%;
    height: 22px;
    padding: 12px;
    margin: 0;
    resize: none;
  }
  .link-input__short-field {
    appearance: none;
    border-radius: 16px;
    border: 1px solid #9f9f9f;
    outline: #000;
    width: 91.78%;
    height: 22px;
    padding: 12px;
    margin: 0;
    resize: none;
  }
  
  .link-input__field:active{
    border-color: #000;
    outline-offset: 0;
  }
  
  .link-input__field:focus {
    border-color: #000;
    outline-offset: 0;
  }
  
  .link-input__button-container {
    display: flex;
    flex-direction: row;
    gap: 12px;
  }
  .link-input__button-delete {
    width: 48px;
    height: 48px;
    padding: 10px;
    background-color: #1a1b22;
    border-radius: 16px;
    border: none;
    background-image: url(../../../../img/delete-button.svg);
    background-size: 20px 20px;
    background-repeat: no-repeat;
    background-position: center;
  }
  .link-input__button-delete:hover {
      background-image: url(../../../../img/delete-button-hover.svg);
  }
  .link-input__button-add {
    width: 48px;
    height: 48px;
    padding: 10px;
    background-color: #1a1b22;
    border-radius: 16px;
    border: none;
    background-image: url(../../../../img/add-button.svg);
    background-size: 20px 20px;
    background-repeat: no-repeat;
    background-position: center;
  }
  .link-input__button-add:hover {
      background-image: url(../../../../img/add-button-hover.svg);
  }